html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}
body.body-scroll {
  padding-top: 11rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

li {
  list-style: none;
}

*::selection {
  background-color: #535353;
  color: rgb(248, 248, 248);
}

/** Media Queries **/
.boton-amarillo {
  display: inline-block;
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-verde {
  display: inline-block;
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-rojo {
  display: inline-block;
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #a40606;
}

.boton {
  display: block;
  width: 100%;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

input:auto-fill {
  outline: 5px solid rgba(0, 0, 0, 0.5);
}

.w-100 {
  width: 100%;
}

/*Content*/
header {
  padding-top: 2rem;
}

header,
main {
  background-color: #EEFBFF;
}

.encabezado {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  padding: 1rem 3rem;
}
@media (max-width: 480px) {
  .encabezado {
    flex-direction: column;
    justify-content: center;
  }
}

.logo source,
.logo img {
  width: 30rem;
  border-radius: 5rem;
}

.contenedor-nav {
  background-color: #EEFBFF;
}
.contenedor-nav.fijo {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  box-shadow: 0px 8px 20px -5px #000000;
}
@media (max-width: 768px) {
  .contenedor-nav.fijo {
    display: none;
  }
}

nav {
  padding: 2rem 0 2rem 0;
  width: 100vh;
}
nav a {
  padding: 1rem 2rem;
  color: #000000;
}
@media (max-width: 768px) {
  nav {
    visibility: hidden;
    opacity: 0;
    height: 0;
  }
}

/*Mobile Menu*/
.mobile-menu {
  display: none;
  background-color: #EEFBFF;
}
@media (max-width: 768px) {
  .mobile-menu {
    display: flex;
    justify-content: center;
    padding-top: 3rem;
  }
}
.mobile-menu svg {
  display: block;
  width: 5rem;
  cursor: pointer;
}

.mostrar {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: auto;
  visibility: visible;
  opacity: 1;
}
@media (max-width: 768px) {
  .mostrar {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.usuario {
  display: flex;
  align-items: center;
  flex-direction: row;
  overflow: hidden;
  position: relative;
  border-radius: 3rem;
  padding: 1rem;
  background: #ffffff;
  box-shadow: 23px 23px 47px #ebebeb, -23px -23px 47px #ffffff;
}
.usuario:hover .ingresar {
  transform: translateY(0);
}
.usuario:hover .m-img {
  transform: translateX(0rem);
}
@media (max-width: 480px) {
  .usuario {
    margin-top: 2rem;
    width: 100%;
  }
  .usuario:hover .m-img {
    transform: translateX(-4rem);
  }
  .usuario:hover .ingresar {
    transform: translateX(-10rem);
  }
}

.ingresar {
  position: relative;
  transform: translateY(8rem);
  transition: 1s ease;
}

.picture {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.picture source,
.picture img {
  width: 8rem;
  transform: translateX(3rem);
  transition: 1s ease;
}

.imagen-principal {
  background-image: linear-gradient(40deg, rgb(108, 172, 228) 0%, rgba(230, 244, 241, 0.5) 100%), url(/build/img/bg-farmacia.webp);
  background-repeat: no-repeat;
  background-position: center center;
  background-image: cover;
  padding: 1rem 2rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  border-radius: 3rem;
  gap: 1rem;
}
@media (max-width: 480px) {
  .imagen-principal {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.imagen-principal-hijo {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.imagen-principal-hijo h1 {
  text-align: left;
}
@media (max-width: 480px) {
  .imagen-principal-hijo h1 {
    text-align: center;
  }
}

.imagen-principal-hijo-img picture {
  height: 100%;
}

.imagen-secundaria {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 8rem;
}
.imagen-secundaria .imagen-secundaria-hijo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 480px) {
  .imagen-secundaria {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.productos {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 5rem;
}
@media (max-width: 480px) {
  .productos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.contenido-producto {
  padding: 0.5rem;
}
.contenido-producto h3, .contenido-producto p {
  margin: 0;
  text-align: center;
}

.producto ul {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding: 0;
}
.producto ul li {
  display: flex;
  justify-content: center;
  align-items: center;
}
.producto ul li picture {
  margin-right: 1rem;
  height: 100%;
  width: 6rem;
}
.producto ul li p {
  font-weight: 900;
}

.producto picture source, img {
  max-width: 100%;
}

.producto-chico {
  padding: 2rem;
  margin-top: 1rem;
}

.producto-chico ul {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding: 0;
}
.producto-chico ul li {
  display: flex;
  justify-content: center;
  align-items: center;
}
.producto-chico ul li picture {
  margin-right: 1rem;
  height: 100%;
  width: 6rem;
}
.producto-chico ul li p {
  font-weight: 900;
}

.imagen-chica {
  max-width: 20rem;
  margin: 0 auto;
}

.nosotros {
  background-image: url(/build/img/fondo-nosotros.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 3rem 5rem;
  border-radius: 3rem;
  margin-top: 10rem;
  gap: 5rem;
}
@media (max-width: 480px) {
  .nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (max-width: 768px) {
  .nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.nosotros-img {
  display: flex;
  align-items: center;
}

.nosotros-info h2 {
  text-align: left;
  font-weight: bold;
}

.formulario {
  /*input[type="text"]{  selector por atributo

  } */
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario textarea {
  resize: none;
  height: 20rem;
}

.img-contenedor {
  height: 40rem;
  overflow: hidden;
  display: flex;
  justify-content: center;
  /* Centra horizontalmente */
  align-items: center;
  /* Centra verticalmente */
}

.img-contenedor picture img {
  object-fit: contain;
  object-position: center center;
}

footer {
  text-align: justify;
}

.footer {
  padding: 2.5rem 0;
  background-color: #e1e1e1;
  margin-top: 5rem;
}
.footer .copyright {
  color: #000000;
  text-align: center;
}

.f-formulario fieldset {
  display: grid;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
}
.f-formulario fieldset label {
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem;
}
.f-formulario fieldset input,
.f-formulario fieldset textarea,
.f-formulario fieldset select {
  width: 95%;
  padding: 1rem;
  outline: none;
  border: none;
  background-color: rgb(228, 228, 228);
  border-radius: 1rem;
  margin: 1rem;
}
.f-formulario fieldset textarea {
  resize: none;
  height: 10rem;
}
.f-formulario .total {
  background-color: rgb(204, 245, 23);
}

table.r-pedidos {
  width: 100%;
  border-spacing: 0;
  margin-top: 5rem;
}
table.r-pedidos thead {
  padding: 2rem;
  background-color: #6CACE4;
}
table.r-pedidos thead th {
  padding: 2rem;
}
table.r-pedidos td {
  text-align: center;
  padding: 2rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.elementos {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.elementos thead {
  background-color: #71B100;
}
table.elementos thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.elementos .imagen-tabla {
  width: 10rem;
  margin: 0 auto;
}

.imagen-small {
  width: 20rem;
}

td {
  text-align: center;
}